<template>
  <div style="flex-direction: column">
   <h2>左</h2>
    <hr>
    <h3>{{right}}</h3>
    <button type="button" class="btn btn-success g-1" @click="$emit('componentLeft',left)">给右边(子-父-子)</button>
  </div>
</template>
<script>
import eventbus from "../../public/eventbus";
export default {
    data(){
      return{
      left: '左边的值',
        right:null,
      }
    },
  created(){
      eventbus.on('componentRight',(msg)=>{
        this.right=msg
      })
  }
}
</script>
