<template>
  <div class="flex-column">
    <h2>右</h2>
    <hr>
    <h3>{{ componentLeft }}</h3>
    <button type="button" class="btn btn-primary g-1" @click="send">给左边(mitt)</button>
  </div>
</template>

<script>
import eventbus from "../../public/eventbus";
export default {
  props: ['componentLeft'],   //在这里对传过来的进行接收,
  data(){
    return{
      right:'右边的值',
    }
  },
  methods:{
    send(){
      eventbus.emit('componentRight',this.right)
    }
  }
}
</script>
